<template>
    <div class="transaction-record-list">
        <div class="field-top">
            <div class="title">
                <p>请选择查看交易类型</p>
            </div>
            <div class="do" @click="select">
                <p v-text="typeVal"></p>
                <div class="border">
                    <div :class="{ 'select-one': !selectAll, 'select-all': selectAll }"></div>
                </div>
            </div>
        </div>
        <div class="field-part">
            <template v-for="item in recordList">
                <mt-cell @click.native="detail(item)" :title="item.tran_type" :label="item.tran_date" :value="item.tran_amount"></mt-cell>
            </template>
        </div>
        <type ref="type" :options="selectOption"></type>
    </div>
</template>

<script>
    import Type from './type';
    export default {
        name: 'transaction-record-list',
        components: {
            Type
        },
        data() {
            return {
                recordList: null,
                typeVal: '全部类型',
                selectAll: true,
                selectOption: {
                    title: '选择交易类型',
                    handler: (item) => {
                        this.panelCall(item);
                    },
                    content: [
                        {
                            title: '行内转账',
                            code: 'bank-in',
                            style: { color: 'red' }
                        },
                        {
                            title: '行外转账',
                            code: 'bank-out'
                        }
                    ]
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('phone-transaction-record');
            if (ls.length > 0) {
                this.recordList = ls;
                this.recordList.filter(item => {
                    item.tran_amount = item.tran_amount + '元';
                });
            }
        },
        methods: {
            detail(item) {
                this.$router.push({ name: 'transactionRecrodInfo', params: { id: item.id } });
            },
            select() {
                this.$refs.type.show();
            },
            panelCall(item) {
                let ls = this.$ls.getItem('phone-transaction-record');
                let _data = ls.filter(val => {
                    val.tran_amount = val.tran_amount + '元';
                    return val.code === item.code;
                });
                this.recordList = _data;
                this.selectAll = false;
                this.typeVal = item.title;
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-top {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        margin-bottom: 10px;
        padding: 20px;
        background-color: #fff;

        .title {
            display: inline-block;
            flex: 1;
        }

        .do {
            display: inline-block;
            p {
                display: inline-block;
            }

            .border {
                display: inline-block;
                border: 1px solid #26a2ff;
                height: 36px;
                width: 36px;
                margin-left: 6px;

                .select-all {
                    display: inline-block;
                    border: 8px solid #26a2ff;
                    vertical-align: top;
                }

                .select-one {
                    display: inline-block;
                    border-bottom: 16px solid #26a2ff;
                    border-left: 16px solid transparent;
                    vertical-align: top;
                }
            }
        }
    }
</style>


