<template>
    <div class="type" :class="{ show : showPanel }">
        <div class="cover" @click="close"></div>
        <div class="main">
            <div class="hd">
                <a href="javascript:;" class="btn-cancel" @click=" close() ">取消</a>
                <h2 v-text="title"></h2>
            </div>
            <div class="content">
                <template v-for="item in list">
                    <cell :style="item.style" @click.native="doBack(item)" :title="item.title"></cell>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'type',
        props: {
            options: { type: Object, required: true }
        },
        data() {
            return {
                showPanel: false
            };
        },
        computed: {
            title() {
                return this.options.title || '选择';
            },
            list() {
                return this.options.content;
            }
        },
        methods: {
            show() {
                this.showPanel = true;
            },
            close() {
                this.showPanel = false;
            },
            doBack(item) {
                this.options.handler(item);
                this.close();
            }
        }
    };

</script>

<style lang="scss" scoped>
    $item-height: 80px;
    .type {
        position: fixed;
        top: 100%;
        left: 0px;
        height: 100%;
        width: 100%;
        z-index: 999;
        opacity: 0;


        @include transition(opacity 0.3s);

        &.show {
            top: 0px;
            opacity: 1;
        }

        .cover {
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            z-index: 1;
            background-color: $mask-rgba;
        }
    }

    .main {
        position: absolute;
        left: 0px;
        right: 0px;
        bottom: 0px;
        z-index: 9;
        background-color: #fff;

        .hd {
            position: relative;
            height: $item-height;
            line-height: $item-height;

            @include mini-border(bottom);

            h2 {
                margin: 0px;
                font-size: 14px;
                font-weight: normal;
                text-align: center;
            }

            .btn-cancel {
                position: absolute;
                top: 0px;
                left: 0px;
                padding: 0px 20px;
                height: 100%;
                color: #4893E3;
            }
        }
    }
</style>


