import ajax from './ajax';
import api from './api';

/*  查询我的积分总数 */
export function queryPoints(data) {
    return ajax.post(api.queryPoints, data);
}
/*  查询我的积分列表 */
export function queryPointsList(data) {
    return ajax.post(api.queryPointsList, data);
}
/*  查询我的积分总数 */
export function queryLoan(data) {
    return ajax.post(api.queryLoan, data);
}
/*  查询我的积分列表 */
export function queryLoanList(data) {
    return ajax.post(api.queryLoanList, data);
}
/*  查询手机交易记录   */
export function queryPhoneTransactionList(data) {
    return ajax.post(api.queryPhoneTransactionList, data);
}


