<template>
    <div class="transaction-record-inf">
        <div class="field-part">
            <mt-cell title="交易类型" :value="recordInfo.tran_type"></mt-cell>
            <mt-cell title="交易日期" :value="recordInfo.tran_date"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="交易金额" :value="recordInfo.tran_amount"></mt-cell>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'transaction-record-info',
        data() {
            return {
                recordInfo: null
            };
        },
        created() {
            let ls = this.$ls.getItem('phone-transaction-record');
            let id = this.$route.params.id;
            let _data = ls.filter(item => {
                return item.id === id;
            });
            this.recordInfo = _data[0];
        }
    };

</script>


